.hero-preview {
  width: 100%;
  max-width: 500px;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.preview-container {
  padding: 1.5rem;
}

.preview-header {
  margin-bottom: 1rem;
}

.preview-header h3 {
  font-size: 1.25rem;
  color: #2d3748;
  margin: 0;
}

.preview-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.preview-image {
  width: 100%;
  aspect-ratio: 16/9;
  overflow: hidden;
  border-radius: 4px;
}

.placeholder-image {
  width: 100%;
  height: 100%;
  background: #edf2f7;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #718096;
}

.preview-text {
  padding: 1rem 0;
}

.preview-text h4 {
  font-size: 1.125rem;
  color: #1a202c;
  margin: 0 0 0.5rem 0;
}

.preview-text p {
  color: #4a5568;
  margin: 0 0 0.5rem 0;
}

.price {
  font-size: 1.25rem;
  font-weight: 600;
  color: #2b6cb0;
} 